.myModal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}

.myModal__content {
  max-width: 40vw;
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
}
