* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: #0a58ca;
  position: relative;
}

.container {
  text-align: center;
  max-width: 50vw;
  margin: 0 auto;
}

.title {
  font-size: 55px;
}

@media (max-width: 950px) {
  .container {
    max-width: 95vw;
  }

  select:focus,
  textarea:focus,
  input:focus {
    font-size: 16px;
  }
}

*,
a:focus {
  outline: none !important;
}
