.listItem {
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  border: 2px solid #0a58ca;
  margin-bottom: 5px;
  font-size: 22px;
  word-break: break-all;
  display: flex;
  justify-content: space-around;
}

.listItem_active {
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  border: 2px solid rgb(248, 76, 2);
  margin-bottom: 5px;
  font-size: 22px;
  word-break: break-all;
  display: flex;
  justify-content: space-around;
  color: rgb(248, 76, 2);
  text-decoration: line-through;
}

.listItem__btn_first {
  margin-right: 10px;
  width: 30px;
  height: 30px;
  background-color: rgb(80, 227, 0);
  border: #fff;
  border-radius: 3px;
  cursor: pointer;
}

.listItem__btn_second {
  margin-right: 10px;
  width: 30px;
  height: 30px;
  background-color: rgb(248, 76, 2);
  border: #fff;
  border-radius: 3px;
  cursor: pointer;
  color: #fff;
}
